/**
 * Основной JavaScript файл для сайта ресторана
 * Обработка плавной прокрутки и анимаций появления секций
 */

// Импорт стилей SCSS
import './styles/main.scss';

// Плавная прокрутка для якорных ссылок
document.querySelectorAll('a[href^="#"]').forEach(anchor => {
  anchor.addEventListener('click', function (e) {
    e.preventDefault();
    const target = document.querySelector(this.getAttribute('href'));
    if (target) {
      target.scrollIntoView({
        behavior: 'smooth',
        block: 'start'
      });
    }
  });
});

// Настройка Intersection Observer для анимации появления секций
const observerOptions = {
  threshold: 0.1,
  rootMargin: '0px 0px -100px 0px'
};

// Создаем наблюдатель для отслеживания видимости секций
const observer = new IntersectionObserver((entries) => {
  entries.forEach(entry => {
    if (entry.isIntersecting) {
      // Добавляем класс visible для запуска анимации
      entry.target.classList.add('visible');
    }
  });
}, observerOptions);

// Применяем наблюдатель ко всем секциям
document.querySelectorAll('section').forEach(section => {
  observer.observe(section);
});

